import React from 'react';
import './App.css';
import {connect} from "react-redux";

class App extends React.Component{
  constructor(props){
    super(props)
    this.state={
        num: null
    }
}
  reset = () =>{
    this.props.reset()
    this.props.resetItems()
  }

  render(){
    return(
      <nav className="navbar navbar-dark bg-dark sidebar">
        <button className="btn btn-light" onClick={()=>this.reset()}>Reset</button>
        <p className="text-monospace text-light">Credits: <span className="text-success font-weight-bold">{this.props.choices.credits}</span></p>
      </nav>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    meta: state.timeReducer,
    choices: state.choiceReducer
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    reset: (payload) => {dispatch({type:"RESET_ALL"})},
    resetItems: (payload) => {dispatch({type:"RESET_ITEMS"})}
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);
